.services{
    // margin-top: 5vh;
    // mix-blend-mode: darken;
    background-color: #D9EEE1;
    align-content: center;
    display: grid;
    grid-template-rows: 80%;
    grid-template-columns: repeat(2, auto);
    color: var(--text-color);
    grid-gap: 1vh;
    padding: 50px 0;
}


.service_container{
    display: grid;
    grid-template-rows: repeat(12, 1fr);
    grid-template-columns: repeat(9, 1fr);
    height: auto;
}

.responsive, .app, .frontend{
    display: grid;
    align-items: center;
    margin: 0;
    grid-row: span 12;
    grid-column: span 3;
    grid-template-rows: 30px auto 1fr;
    grid-template-columns: 1fr;
    padding: 0 5vw;
    align-content: center;
    background-color: #D9EEE1;
    color: #1B1C1E;
    border-right: 2px solid #1B1C1E;
}

.frontend{
    border: none;
}

.service_name, .s_icon{
    height: auto;
}

.service_name{
    font-size: 22px;
    font-weight: 500;
}

.s_description{
    padding-top: 10px;
    font-size: 15px;
    color: #1B1C1E;
}


.service_head{
    display: grid;
    align-items: center;
    justify-content: start;
    font-size: 2vw;
}

.service_head .head{
    display: grid;
    align-content: center;
    height: 70%;
    width: 15vw;
    mix-blend-mode: normal !important;
    border-top-right-radius: 100px;
    border-bottom-right-radius: 100px;
    background-color: var(--bg-color);
    box-shadow: 0 0 20px 0px #0000001b;
    padding: 0 20px;
    min-width: 150px;
    max-width: 300px;
    font-weight: 500;
}

.head span{
    font-weight: bold;
}

.responsive .s_icon{
    color: #0DB898;
}

.app .s_icon{
    color: #854BEB;
}

.frontend .s_icon{
    color: #ff69b7;
}


@media screen and (max-width: 768px) {
    .services{
        height: 92vh;
        min-height: 600px;
        grid-template-rows: repeat(2, auto);
        grid-template-columns: auto;
        padding: 50px 0;
    }
    .service_container{
        max-width: 100vw;
        row-gap: 50px;
        padding: 0 20px;
    }

    .service_name{
        font-size: 25px !important;
        font-weight: 600;
    }
    
    .s_description{
        font-size: 16px;
    }
    
    .responsive, .app, .frontend{
        grid-row: span 4;
        grid-column: span 8;
        border: none;
    }


    .service_head{
        height: 150px;
        width: auto;
        font-size: 30px !important;
    }
    .service_head .head{
        padding: 0 30px;
        max-width: 200px !important;
        width: 40vw;
        height: 120px;
        // font-size: 18px;
    }

    .service_name{
        font-size: 4vw;
    }
}

@media screen and (max-width: 1024px) and (min-width: 769px) {
    .services{
        height: 500px !important;
        min-height: 400px !important;
        grid-template-rows: repeat(2, auto);
        grid-template-columns: 1fr;
        align-content: start;
        row-gap: 50px;
    }

    .service_head{
        height: 200px;
        width: auto;
        font-size: 6.5vw;
    }

    .service_head .head{
        width: 50vw;
        font-size: 40px;
    }
}


@media screen and (max-width: 820px) {
    .responsive, .app, .frontend{
        padding: 0 10px;
    }
}

@media screen and (min-width: 1440px) {
    .services{
        grid-template-rows: 75%;
    }
}