.info{display:grid;grid-template-rows:repeat(3, auto);grid-template-columns:1fr;grid-gap:10px;align-content:center;align-self:center}#name{font-size:5vw;font-weight:400;height:22.5vw}#description{font-size:1.3vw;color:var(--n-color) !important}strong{font-weight:600 !important;color:var(--s-color)}.button{position:relative;display:flex;justify-content:center;align-items:center;width:180px;height:50px;margin-top:20px;background-color:var(--a-color);font-size:20px;font-weight:500;font-family:cursive;color:#f5f5f5;cursor:pointer;border-radius:8px;border:none;transition:all .5s;transition-duration:.4s;text-decoration:none;overflow:hidden;outline:none;letter-spacing:.5px;user-select:none}.button span{cursor:pointer;display:inline-block;position:relative;transition:.5s}.button span:after{content:"»";position:absolute;opacity:0;top:0;right:-20px;transition:.5s}.button:hover span{padding-right:25px}.button:hover span:after{opacity:1;right:0}.button:after{content:"";background:rgba(255,255,255,.2901960784);display:block;position:absolute;padding-top:350%;padding-left:300%;margin-left:0px !important;margin-top:0%;opacity:0;transition:all .9s;border-radius:100%}.button:active:after{padding:0;margin:0;opacity:1;transition:0s}.button:active{transform:scale(0.85)}